.refferaldiv {
  background: #0cb2a2;
  position: absolute;
  height: 100vh;
  width: 100%;
  top: 0;
}
.refferaldiv::before {
  content: " ";
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  opacity: 0.03;
  background-image: url(../../public/images/blackboard.webp);
  background-repeat: repeat;
  background-blend-mode: screen;
  background-size: contain;
}
.child {
  position: absolute;
  bottom: -1%;
  width: 100%;
}

.what-you-get {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  width: 100%;
  flex-direction: column;
  padding: 100px 0;
  width: var(--fixedWidth);
}
.what-you-get-section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  background: #f0fbfa;
}

.grass-section {
  padding: 100px 0;
  width: 100%;
}

.grass-section .grass-with-boy {
  height: 70vh;
  width: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom;
}

.what-you-get-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  background: #def8f5;
  width: fit-content;
  padding: 50px;
  margin-left: 10rem;
  border-radius: 10px;
}
.what-you-get-card h3 {
  font-size: 2rem;
  color: #028172;
  text-transform: uppercase;
  font-weight: 800;
}
.what-you-get ul {
  /* padding: 20px; */
  margin: 0;
  padding-inline-start: 0;
}
.what-you-get ul li {
  list-style: none;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 25px 0;
  color: #028172;
  font-size: 1.3rem;
  font-weight: 400;
}
.what-you-get ul li img {
  width: 25px;
  margin-right: 20px;
}
@media screen and (max-width: 1550px) {
  /*What You Get*/
  .what-you-get-card {
    margin-left: 2rem;
  }
}
@media screen and (max-width: 800px) {
  /*What You Get*/
  .grass-section,
  .grass-with-boy {
    width: 90vw !important;
  }
}
@media screen and (max-width: 600px) {
  .what-you-get-card {
    margin: 1rem;
  }
  .what-you-get {
    width: 90%;
  }
}
@media screen and (max-width: 400px) {
  .what-you-get-card {
    margin: 1rem;
  }
  .grass-section {
    padding: 50px 0;
    width: 100%;
  }
  .what-you-get-card h3 {
    font-size: 1.1rem;
  }
  .what-you-get-card ul li {
    font-size: 0.9rem;
  }
  .what-you-get-card img {
    width: 20px !important;
  }
}

.password {
  border-radius: 0.25rem;
  background-color: #fff;
}
